						{{ this.budget.perimeter }} metros de permetro, {{ this.budget.medium_depth }} metros de profundidade mdia, {{ this.budget.m2_wall }} metros de paredes e {{ this.budget.m2_facial }} metros
						faciais
						<span v-if="this.budget.beach">e prainha externa medindo ({{ this.budget.beach_length }}) x ({{ this.budget.beach_width }}) x ({{ this.budget.beach_medium_depth }}).</span>
