							<li v-if="item.type == 'filters'">Fornecimento e instalao de 1 filtro {{ item.equipment.name }} {{ item.equipment.brands ? 'da marca ' + item.equipment.brands.name : '' }}</li>
							<li v-if="item.type == 'sands'">Fornecimento de sacos de areia para filtro {{ item.equipment.name }} {{ item.equipment.brands ? 'da marca ' + item.equipment.brands.name : '' }}</li>
							<li v-if="item.type == 'engines'">Fornecimento e instalao de 1 motobomba {{ item.equipment.name }} {{ item.equipment.brands ? 'da marca ' + item.equipment.brands.name : '' }}</li>
							<li v-if="item.type == 'lids'">
